.program-title {
    img {
        height: 5em;
    }
}

.carousel {
    box-shadow: rgba(136, 165, 191, 0.48) 6px 2px 16px 0px, rgba(255, 255, 255, 0.8) -6px -2px 16px 0px;
    width: 100%;
    cursor: grab;
    overflow: hidden;
    border-radius: 10px;
    background: rgba(255, 255, 255, 0.25);
    backdrop-filter: blur(12px);
    -webkit-backdrop-filter: blur(12px);


    .inner-carousel {
        display: flex;
        width: max-content;

        .app__program-item {
            width: 300px;
            flex-direction: column;
            margin: 2rem;
            padding: 1rem;
            border-radius: 0.5rem;
            background-color: #fff;
            color: #000;

            cursor: pointer;
            transition: all 0.3s ease;

            &:hover {
                box-shadow: 0 0 25px rgba(0, 0, 0, 0.2);
            }


            @media screen and (min-width: 2000px) {
                width: 470px;
                padding: 1.25rem;
                border-radius: 0.75rem;
            }


            @media screen and (max-width: 300px) {
                width: 100%;
                margin: 1rem;
            }
        }
    }

    .app__program-img {
        width: 100%;
        height: 230px;

        position: relative;

        img {
            width: 100%;
            height: 100%;
            border-radius: 0.5rem;
            object-fit: cover;
        }

        @media screen and (min-width: 2000px) {
            height: 350px;
        }
    }

    .app__program-hover {
        position: absolute;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
        width: 100%;
        height: 100%;
        background-color: rgba(0, 0, 0, 0.5);
        border-radius: 0.5rem;
        opacity: 0;
        transition: all 0.3s ease;

        div {
            width: 50px;
            height: 50px;
            border-radius: 50%;
            background-color: rgba(0, 0, 0, 0.5);
            color: #fff;
            margin: 1rem;
            font-family: var(--font-base);
            font-weight: 800;
            cursor: pointer;
            transition: all 0.3s ease;

            svg {
                width: 50%;
                height: 50%;
                color: var(--white-color);
            }
        }
    }

    .app__program-content {
        padding: 0.5rem;
        width: 100%;
        position: relative;
        flex-direction: column;

        h4 {
            margin-top: 1rem;
            line-height: 1.5;
        }

        .app__program-tag {
            position: absolute;
            padding: 0.5rem 1rem;
            border-radius: 10px;
            background-color: #fff;
            top: -25px;
        }
    }
}